export default {
	install(Vue) {
		Vue.mixin({
			data() {
				return {
					formRules: {
						loginId: {
							rules: [{
									required: true,
									errorMessage: '',
								},
								{
									minLength: 5,
									maxLength: 12,
									errorMessage: ' {minLength}  {maxLength} ',
								}
							]
						},
						loginPwd: {
							rules: [{
								required: true,
								errorMessage: '',
							}, ]
						},
						phoneNumber: {
							rules: [{
									required: true,
									errorMessage: '',
								},
								{
									validateFunction: function(rule, value, data, callback) {
										const reg =
											/^(0|86|17951)?(13[0-9]|15[012356789]|166|199|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
										if (!reg.test(value)) {
											callback('')
										}
									}
								}
							]
						},
						verificationCode: {
							rules: [{
									required: true,
									errorMessage: '',
								},
								{
									validateFunction: function(rule, value, data, callback) {
										if (value !== data.remoteVerificationCode) {
											callback('')
										}
									}
								}
							]
						},

					}
				}
			}
		})
	}
}
