import {
	mapState,mapMutations
} from "vuex"

export default {
	install(Vue) {
		Vue.mixin({
			computed: {
				...mapState('userInfo', ['userInfo']) //,()
			},
			methods: {
				whoAmI() {
					// 
					return new Promise((resolve, reject) => {
						if (this.userInfo) {
							// 
							resolve();
						} else {
							// 
							uni.navigateTo({
								url: '/pages/userInfo/login/login'
							})
						}
					})
				},
				...mapMutations('userInfo', ['initUserInfo']), //()
			},
		})
	}
}
